import { useEffect, useState } from "react";
import "./Style.scss";
import bg from "../../assets/bg.png";
import { Button, Checkbox, ConfigProvider, Form, Input, InputNumber, Select } from "antd";
import { useParams } from "react-router-dom";

const { Option } = Select;

function Main() {
    const { invitationCode } = useParams();
    const [userInput, setUserInput] = useState();

    const initialValues = {
        invitationCode: invitationCode,
    };

    useEffect(() => {
        window.scrollTo(0, 0);
        window.addEventListener(
            "scroll",
            () => {
                document.body.style.setProperty("--scroll", window.pageYOffset / (document.body.offsetHeight - window.innerHeight));
            },
            false
        );
    }, []);

    function handleSubmit(values) {
        console.log(values);
    }

    function handleFieldsChange(changedValues, allValues) {
        setUserInput(allValues);
    }

    return (
        <div className="main-cont">
            <div className="cover-image-wrapper">
                <div className="cover-image" />
            </div>
            <div className="cover-name-wrapper">
                <div className="cover-name" />
            </div>
            <div className="cover-date-wrapper">
                <div className="cover-date" />
            </div>

            <div className="content-image-wrapper">
                <img className="content-image" alt="" src={bg} />
            </div>
            <div className="content-text-wrapper">
                <div className="text-container">
                    <div className="desc">Request the pleasure of your company at their wedding celebration</div>
                    <div className="day">WEDNESDAY</div>
                    <div className="date">29. NOV. 23</div>
                    <div className="venue">Restaurant New Palm Grove</div>
                    <div className="address">Lot. P.T.20083, Jalan Prai 1, Taman Meru, 41050 Klang, Selangor</div>
                    <div className="time">Dinner at 7.00 pm</div>
                    <div className="rsvp">Kindly RSVP by 31th October 2023</div>
                    <div className="contact">Huiyao +6011-2018 1580 | Yeemay +6016-511 3013</div>
                </div>
            </div>
            <div className="rsvp-form-wrapper">
                <ConfigProvider
                    theme={{
                        token: {
                            fontFamily: "Montserrat-Regular",
                        },
                    }}
                >
                    <Form requiredMark={false} initialValues={initialValues} onFinish={handleSubmit} onValuesChange={handleFieldsChange}>
                        <Form.Item name="invitationCode">
                            <Input disabled placeholder="Invitation Code" />
                        </Form.Item>

                        <Form.Item
                            label="Are you attending?"
                            name="attendance"
                            rules={[
                                {
                                    required: true,
                                    message: "This field is required.",
                                },
                            ]}
                        >
                            <Select placeholder="Please Select">
                                <Option value={true}>Yes</Option>
                                <Option value={false}>No</Option>
                            </Select>
                        </Form.Item>

                        <Form.Item
                            label="Number of guest"
                            name="pax"
                            hidden={!userInput?.attendance}
                            rules={[
                                {
                                    required: userInput?.attendance,
                                    message: "This field is required.",
                                },
                            ]}
                        >
                            <Select placeholder="Please Select">
                                <Option value={1}>1</Option>
                                <Option value={2}>2</Option>
                            </Select>
                        </Form.Item>

                        <Button disabled={userInput?.attendance === undefined} block htmlType="submit">
                            Submit
                        </Button>
                    </Form>
                </ConfigProvider>
            </div>
        </div>
    );
}

export default Main;
